'use client'

import React, { useState, useEffect, useCallback } from "react";
import { useRouter } from "next/navigation";
import { getToken, isAuthenticated } from "@/lib/utils/auth";
import { SidebarComponent } from "@/components/Sidebar/SidebarComponent";
import { searchUsersByUserName, followUser, unfollowUser } from "@/lib/api/userApi";
import { UserProfile } from "@/types/api.types";
import { Search as SearchIcon, X } from "lucide-react";
import toast from "react-hot-toast";

export default function SearchPage() {
    const [searchQuery, setSearchQuery] = useState("");
    const [searchResults, setSearchResults] = useState<UserProfile[]>([]);
    const [isSearching, setIsSearching] = useState(false);
    const [isMobile, setIsMobile] = useState(false);
    const [sidebarOpen, setSidebarOpen] = useState(false);
    const router = useRouter();

    // Check authentication
    useEffect(() => {
        if (typeof window === 'undefined') return;
        if (!isAuthenticated()) {
            router.push('/login');
        }
    }, [router]);

    // Check mobile
    useEffect(() => {
        if (typeof window === 'undefined') return;
        const checkMobile = () => {
            setIsMobile(window.innerWidth < 768);
        };
        checkMobile();
        window.addEventListener('resize', checkMobile);
        return () => window.removeEventListener('resize', checkMobile);
    }, []);

    // Debounced search
    useEffect(() => {
        const token = getToken();
        if (!token || !searchQuery.trim()) {
            setSearchResults([]);
            return;
        }

        const timeoutId = setTimeout(async () => {
            setIsSearching(true);
            try {
                const results = await searchUsersByUserName(searchQuery);
                setSearchResults(results || []);
            } catch (err) {
                console.error('Search error:', err);
            } finally {
                setIsSearching(false);
            }
        }, 300);

        return () => clearTimeout(timeoutId);
    }, [searchQuery]);

    // Handle follow
    const handleFollow = async (userId: string) => {
        try {
            await followUser(userId);
            toast.success('Followed successfully');
            setSearchResults(prev => prev.map(u =>
                u.userId === userId ? { ...u, isFollowing: true } : u
            ));
        } catch (err) {
            console.error('Follow error:', err);
            toast.error('Could not follow user');
        }
    };

    // Handle unfollow
    const handleUnfollow = async (userId: string) => {
        try {
            await unfollowUser(userId);
            toast.success('Unfollowed');
            setSearchResults(prev => prev.map(u =>
                u.userId === userId ? { ...u, isFollowing: false } : u
            ));
        } catch (err) {
            console.error('Unfollow error:', err);
            toast.error('Could not unfollow user');
        }
    };

    // Navigate to profile
    const handleViewProfile = (userId: string) => {
        router.push(`/user/${userId}`);
    };

    return (
        <div className="flex min-h-screen">
            {/* Mobile menu button */}
            {isMobile && (
                <button
                    onClick={() => setSidebarOpen(!sidebarOpen)}
                    className="fixed top-4 left-4 z-50 p-2 bg-white rounded-md shadow-md border md:hidden"
                    aria-label="Toggle menu"
                >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        {sidebarOpen ? (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        ) : (
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                        )}
                    </svg>
                </button>
            )}

            {/* Mobile overlay */}
            {isMobile && sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black bg-opacity-50 z-40 md:hidden"
                    onClick={() => setSidebarOpen(false)}
                />
            )}

            {/* Sidebar - Desktop */}
            <div className="hidden md:block w-[240px] h-screen bg-white border-r flex-shrink-0">
                <SidebarComponent />
            </div>

            {/* Sidebar - Mobile */}
            <div
                className={`
                    md:hidden fixed top-0 left-0 w-64 h-screen bg-white border-r z-50
                    transition-transform duration-300 ease-in-out
                    ${sidebarOpen ? 'translate-x-0' : '-translate-x-full'}
                `}
            >
                <SidebarComponent />
            </div>

            {/* Main Content */}
            <div className={`flex-1 min-h-screen w-full max-w-full overflow-x-hidden ${isMobile ? 'pt-16 px-4' : 'pl-3'}`}>
                <div className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
                    <div className="max-w-2xl mx-auto px-4 py-8">
                        {/* Search Header */}
                        <h1 className="text-2xl font-bold text-gray-800 mb-6">Search</h1>

                        {/* Search Input */}
                        <div className="relative mb-6">
                            <SearchIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Search users..."
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                                className="w-full pl-12 pr-12 py-4 border border-gray-200 rounded-2xl focus:outline-none focus:ring-2 focus:ring-sky-500 focus:border-transparent transition-all bg-white text-gray-800"
                            />
                            {searchQuery && (
                                <button
                                    onClick={() => setSearchQuery("")}
                                    className="absolute right-4 top-1/2 -translate-y-1/2 p-1 hover:bg-gray-100 rounded-full transition-colors"
                                >
                                    <X className="w-5 h-5 text-gray-400" />
                                </button>
                            )}
                        </div>

                        {/* Search Results */}
                        <div className="space-y-2">
                            {isSearching ? (
                                <div className="text-center py-8 text-gray-500">
                                    Searching...
                                </div>
                            ) : searchQuery && searchResults.length > 0 ? (
                                searchResults.map((user) => (
                                    <div
                                        key={user.id || user.userId}
                                        className="bg-white rounded-xl p-4 flex items-center gap-4 hover:shadow-md transition-shadow"
                                    >
                                        <img
                                            src={user.imageUrl || "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_640.png"}
                                            alt="Avatar"
                                            className="w-14 h-14 rounded-full object-cover cursor-pointer"
                                            onClick={() => handleViewProfile(user.userId || user.id || '')}
                                        />
                                        <div className="flex-1 min-w-0">
                                            <p
                                                className="font-semibold text-gray-800 cursor-pointer hover:text-sky-600 transition-colors"
                                                onClick={() => handleViewProfile(user.userId || user.id || '')}
                                            >
                                                {user.firstName} {user.lastName}
                                            </p>
                                            <p className="text-sm text-gray-500">@{user.username}</p>
                                        </div>
                                        <button
                                            onClick={() => user.isFollowing
                                                ? handleUnfollow(user.userId || user.id || '')
                                                : handleFollow(user.userId || user.id || '')
                                            }
                                            className={`px-4 py-2 rounded-lg font-medium transition-all ${user.isFollowing
                                                    ? 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                                                    : 'bg-gradient-to-r from-sky-500 to-blue-500 text-white hover:from-sky-600 hover:to-blue-600'
                                                }`}
                                        >
                                            {user.isFollowing ? 'Following' : 'Follow'}
                                        </button>
                                    </div>
                                ))
                            ) : searchQuery ? (
                                <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-12 text-center">
                                    <div className="w-24 h-24 mx-auto mb-6 rounded-full bg-gradient-to-br from-sky-100 to-blue-100 flex items-center justify-center">
                                        <SearchIcon className="w-12 h-12 text-sky-400" />
                                    </div>
                                    <h3 className="text-xl font-bold text-gray-800 mb-2">No results found</h3>
                                    <p className="text-gray-500 text-sm">Try a different search term</p>
                                </div>
                            ) : (
                                <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-12 text-center">
                                    <div className="w-24 h-24 mx-auto mb-6 rounded-full bg-gradient-to-br from-sky-100 to-blue-100 flex items-center justify-center">
                                        <SearchIcon className="w-12 h-12 text-sky-400" />
                                    </div>
                                    <h3 className="text-xl font-bold text-gray-800 mb-2">Find people</h3>
                                    <p className="text-gray-500 text-sm">Search for friends by username or name</p>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
